<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-card 
      class="mx-auto"
      max-width="700"
      outlined
    >
      <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
        Change Password
      </v-card-title>
      <v-card-text class="my-3">
        <v-container>
          <v-text-field
            label="Current Password"
            v-model="current"
            outlined
            hide-details="auto"
            :type="showCurrentPassword ? 'text' : 'password'"
            :append-icon="showCurrentPassword ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="showCurrentPassword = !showCurrentPassword"
            dense
          />
        </v-container>
        <v-container>
          <v-text-field
            label="New Password"
            v-model="new1"
            outlined
            hide-details="auto"
            :type="showNew1Password ? 'text' : 'password'"
            :append-icon="showNew1Password ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="showNew1Password = !showNew1Password"
            dense
          />
        </v-container>
        <v-container>
          <v-text-field
            label="Confirm New Password"
            v-model="new2"
            outlined
            hide-details="auto"
            :type="showNew2Password ? 'text' : 'password'"
            :append-icon="showNew2Password ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="showNew2Password = !showNew2Password"
            dense
          />
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="success" :disabled="new1!=new2 || !current || !new1 || !new2" @click="change">Change</v-btn>
      </v-card-actions>
    </v-card>
    <v-snackbar
        v-model="snackbar"
        :absolute="absolute"
        color="secondary"
        class="align-content-lg-space-between"
    >
      {{ message }}
      <template v-slot:action="{ attrs }">
        <v-btn  v-bind="attrs" color="warning" text @click="snackbar = false"
        >Close</v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      absolute: true,
      snackbar: false,
      message: "",
      overlay: false,
      showCurrentPassword: false,
      showNew1Password: false,
      showNew2Password: false,
      current: '',
      new1: '',
      new2: ''
    }
  },
  methods: {
    change() {
      if(this.new1.length < 8) {
        this.message = "Password length must be atleast 8 characters"
        this.snackbar = true
        return
      }
      this.overlay = true;
      let body = {
        currentPassword: this.current,
        newPassword: this.new1
      }
      fetch("/site/user/change-password", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual",
        body: JSON.stringify(body),
      }).then((response) => {
        this.overlay = false;
        if(response.status === 200) {
          this.$store.commit("setMessage", {
            type: "success",
            text: "Password Changed",
          });
          setTimeout(() => {
            fetch("/auth/logout").then(() => {
              this.loading = false
              this.$store.commit('logout')
              this.$emit('loggedout')
              // this.$store.commit('setMessage', { type: 'success', text: this.$t('App.hardcoded-texts.You have logged out.') } )
              this.$router.push( {path: "/" } )
            })
          }, 800)
        } else {
          response.json().then((data) => {
            this.$store.commit("setMessage", {
              type: "error",
              text: data.message
            });
          })
        }
      }).catch(() => {
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to change password."
        });
      })
    }
  },
}
</script>